button,
.button {
    background-clip: padding-box;
    border-radius: 0.25rem;
    user-select: none;
    -webkit-touch-callout: default;
    -webkit-touch-callout: none;
    transition: background-color 0.2s ease 0s;
    will-change: background-color, height, padding-bottom, margin-bottom, box-shadow, transform;
    background-color: #507791;
    background-color: var(--color-bowhead);
    color: #f9f9f9;
    color: var(--color-white);
    min-width: 8em;
    height: 2.182em;
    display: inline-block;
    padding: 0 0.728em 0.182em;
    border-width: 0;
    margin-top: 0.25rem;
    margin-top: var(--border-width-default);
    box-shadow: inset 0 -0.182em 0 0 rgba(6, 6, 6, 0.1);
    box-shadow: inset 0 -0.182em 0 0 var(--color-black--gamma);
    overflow: visible;
    position: relative;
    font-size: 1.125rem;
    font-size: var(--font-size-gamma);
    font-weight: 600;
    font-weight: var(--font-weight-bold);
    line-height: 2;
    line-height: var(--line-height-code);
    text-align: center;
    text-decoration: none;
    text-shadow: 0 1px 1px rgba(6, 6, 6, 0.1);
    white-space: nowrap;
    cursor: pointer;
    appearance: none;
}

button:focus,
.button:focus {
    outline: 0.125rem solid #5f8aa6;
    outline: var(--border-width-thin) var(--border-style-default) var(--color-raven);
    outline-offset: 0.125rem;
    outline-offset: var(--border-width-thin);
}

button:hover, button:focus, button:active,
.button:hover,
.button:focus,
.button:active {
    background-color: #5f8aa6;
    background-color: var(--color-raven);
    text-decoration: none;
}

button:active,
.button:active {
    height: 2em;
    padding-bottom: 0;
    margin-bottom: 0.182em;
    margin-bottom: var(--border-width-button);
    box-shadow: inset 0 -1px 0 0 #507791;
    box-shadow: inset 0 calc(-1 * var(--border-width-hairline)) 0 0 var(--color-bowhead);
    transform: translateY(0.182em);
    transform: translateY(var(--border-width-button));
}

button::before, button::after,
.button::before,
.button::after {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    content: "";
    height: 0.182em;
    height: var(--border-width-button);
}

button:active::before, button:active::after,
.button:active::before,
.button:active::after {
    top: -0.182em;
    top: -var(--border-width-button);
}

button[disabled],
.button[disabled] {
    opacity: 0.7;
    opacity: var(--opacity-beta);
    pointer-events: none;
}

button .icon,
.button .icon {
    filter: drop-shadow(0 0 1px rgba(6, 6, 6, 0.1));
}

button {
    -webkit-font-smoothing: inherit;
}

button::-moz-focus-inner {
    padding: 0;
    margin: 0;
}

button::-moz-focusring {
    outline: 1px dotted ButtonText;
    outline: var(--border-width-hairline) var(--border-style-alternate) ButtonText;
}

.button--small {
    min-width: 5em;
    font-size: 1.25rem;
    font-size: var(--font-size-large);
    height: 2em;
    line-height: 1.818;
}

.button--small:active {
    height: 1.818em;
}

