    position: absolute;
    top: 0;
    right: 0;
    bottom: 1.25rem;
    left: 0;
}

.media {
    width: 100%;
    height: auto;
    overflow: hidden;
    position: relative;
}

.media--16-by-9, .media--vimeo,
.media--youtube {
    padding-top: 56.25%;
}

.media--4-by-3, .media--speakerdeck {
    padding-top: 75%;
}

.pixelated {
    image-rendering: pixelated;
}

.cp_embed_wrapper {
    margin-bottom: 1.25rem;
    margin-bottom: var(--measure-medium);
    resize: both;
    overflow: auto;
}

.cp_embed_wrapper iframe {
    margin-bottom: 0;
}

.sparkline {
    user-select: none;
    -webkit-touch-callout: default;
    -webkit-touch-callout: none;
    transition: all 0.2s ease;
    width: 10rem;
    height: 1.5rem;
}

.sparkline:hover {
    background-color: var(--color-highlight);
    outline: 0.125rem solid gold;
    outline: var(--border-width-thin) var(--border-style-default) var(--color-highlight);
}

.sparkline:active {
    background-color: #eb2d37;
    background-color: var(--color-canada);
    outline: 0.125rem solid #eb2d37;
    outline: var(--border-width-thin) var(--border-style-default) var(--color-canada);
}

.ingredients {
    display: flex;
    align-items: space-between;
    flex-wrap: wrap;
}

@media print {
    nav,
    .widget,
    .comments,
    footer .home-anchor,
    footer .style-guide-anchor {
        display: none;
        visibility: hidden;
    }
    button,
    .button {
        color: #4f4f4f;
        color: var(--color-kaiser);
        border-width: 1px;
        border-width: var(--border-width-hairline);
        border-style: solid;
        border-style: var(--border-style-default);
        border-color: #4f4f4f;
        border-color: var(--color-kaiser);
        box-shadow: none;
    }
    
    p {
        orphans: 3;
        widows: 3;
    }
    kbd {
        box-shadow: none;
    }
}

.page--style-guide .lede + h3 {
    margin-top: 0;
}

.palette-list {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

@media screen and (max-width: 500px) {
    .palette-list {
        flex-direction: column;
    }
}

.palette-list li {
    font-variant-numeric: lining-nums tabular-nums;
    font-feature-settings: "lnum", "tnum";
    padding: 1.25rem 0;
    padding: var(--measure-medium) 0;
    vertical-align: middle;
    font-size: 0.875rem;
    font-size: var(--font-size-small);
    text-align: center;
    white-space: nowrap;
}

@media screen and (max-width: 500px) {
    .palette-list li {
        flex: 1 0 100%;
    }
}

@media screen and (min-width: 501px) and (max-width: 1140px) {
    .palette-list li {
        flex: 1 0 50%;
    }
}

@media screen and (min-width: 1141px) {
    .palette-list li {
        flex: 1 0 33.333%;
    }
}

.palette-list li[data-color="bowhead"] {
    background-color: #507791;
    color: #f9f9f9;
}

.palette-list li[data-color="raven"] {
    background-color: #5f8aa6;
    color: #f9f9f9;
}

.palette-list li[data-color="coyote"] {
    background-color: #759ab3;
    color: #f9f9f9;
}

.palette-list li[data-color="canada"] {
    background-color: #eb2d37;
    color: #f9f9f9;
}

.palette-list li[data-color="black"] {
    background-color: #060606;
    color: #f9f9f9;
}

.palette-list li[data-color="mineshaft"] {
    background-color: #2b2b2b;
    color: #f9f9f9;
}

.palette-list li[data-color="kaiser"] {
    background-color: #4f4f4f;
    color: #f9f9f9;
}

.palette-list li[data-color="dove"] {
    background-color: #737373;
    color: #f9f9f9;
}

.palette-list li[data-color="alto"] {
    background-color: #dddddd;
    color: #4f4f4f;
}

.palette-list li[data-color="white"] {
    background-color: #f9f9f9;
    color: #4f4f4f;
}

.palette-list li h4 {
    margin-bottom: 0;
    line-height: 1.875;
    line-height: var(--line-height-default);
}

.transition-example {
    position: relative;
    margin-bottom: 3.5rem;
}

.transition-example::before {
    position: absolute;
    top: 100%;
    left: 50%;
    transform: translateX(-50%);
    content: "(Hover to interact.)";
    color: #4f4f4f;
    color: var(--color-kaiser);
    margin-top: 0.625rem;
    margin-top: var(--measure-small);
}

@media (pointer: coarse) {
    .transition-example::before {
        content: "(Tap to interact.)";
    }
}

.transition-example__demo {
    background-clip: padding-box;
    border-radius: 0.25rem;
    transition: color 0.2s ease 0s;
    width: 100%;
    max-width: calc(336px + (480 - 336) * ((100vw - 500px) / (1140 - 500)));
    max-width: var(--line-length-responsive);
    height: 3.375rem;
    display: block;
    border-width: 0.125rem;
    border-width: var(--border-width-thin);
    border-style: solid;
    border-style: var(--border-style-default);
    border-color: #dddddd;
    border-color: var(--color-alto);
    margin-left: auto;
    margin-right: auto;
    overflow-x: hidden;
    position: relative;
    line-height: 3.125;
    text-align: center;
    z-index: 2;
    z-index: var(--z-index-above);
}

.transition-example__demo:hover, .transition-example__demo:focus {
    color: #f9f9f9;
    color: var(--color-white);
}

.transition-example__demo::before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-clip: padding-box;
    border-radius: 0.125rem;
    content: "";
    background-color: #4f4f4f;
    background-color: var(--color-kaiser);
    z-index: -1;
    z-index: var(--z-index-below);
}

.transition-example--slide .transition-example__demo {
    cursor: e-resize;
}

.transition-example--slide .transition-example__demo::before {
    transition: transform 0.2s ease 0s;
    transform: translateX(-101%);
}

.transition-example--slide .transition-example__demo:hover::before, .transition-example--slide .transition-example__demo:focus::before {
    transform: translateX(0);
}

.transition-example--fade .transition-example__demo {
    cursor: cell;
}

.transition-example--fade .transition-example__demo::before {
    transition: opacity 0.2s ease 0s;
    opacity: 0;
}

.transition-example--fade .transition-example__demo:hover::before, .transition-example--fade .transition-example__demo:focus::before {
    opacity: 1;
}

.icons-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.icons-list li {
    display: inline-block;
    margin-left: 1.25rem;
    margin-left: var(--measure-medium);
    margin-right: 1.25rem;
    margin-right: var(--measure-medium);
}

